// Types
interface WarmupMailbox {
  id: string
  email: string
  displayName: string
  provider: 'gmail' | 'outlook' | 'microsoft365' | 'smtp' | 'sendgrid' | 'mailgun'
  status: 'pending' | 'connecting' | 'warming' | 'ready' | 'paused' | 'error'
  warmupMode: 'conservative' | 'balanced' | 'aggressive'
  currentDailyLimit: number
  dailyLimitMax: number
  warmupDuration: number
  warmupProgress: number // percentage 0-100
  warmupStartDate: string | null
  reputationScore: number
  inboxPlacementRate: number
  openRate: number
  replyRate: number
  bounceRate: number
  totalSent: number
  totalReceived: number
  totalReplies: number
  todaySent: number
  todayReceived: number
  lastSentAt: string | null
  lastError: string | null
  healthScore: number
  connectionStatus: 'connected' | 'disconnected' | 'error'
}

interface WarmupStats {
  totalMailboxes: number
  activeMailboxes: number
  readyMailboxes: number
  errorMailboxes: number
  avgReputationScore: number
  avgInboxRate: number
  totalSentToday: number
  totalReceivedToday: number
  totalRepliesToday: number
}

interface DailyMetric {
  date: string
  sent: number
  received: number
  opens: number
  replies: number
  bounces: number
  inboxRate: number
  reputationScore: number
}

interface NetworkPeer {
  id: string
  email: string
  trustScore: number
  sentCount: number
  receivedCount: number
  replyCount: number
  lastInteraction: string | null
  status: 'active' | 'paused' | 'blocked'
}

interface WarmupTemplate {
  id: string
  name: string
  category: string
  subjects: string[]
  bodies: string[]
  usageCount: number
  replyRate: number
  isSystem: boolean
}

