// Helper Functions
const getStatusColor = (status: string) => {
  switch (status) {
    case 'ready': return 'bg-emerald-100 text-emerald-700 border-emerald-200'
    case 'warming': return 'bg-blue-100 text-blue-700 border-blue-200'
    case 'pending': return 'bg-gray-100 text-gray-700 border-gray-200'
    case 'paused': return 'bg-amber-100 text-amber-700 border-amber-200'
    case 'error': return 'bg-red-100 text-red-700 border-red-200'
    case 'connecting': return 'bg-purple-100 text-purple-700 border-purple-200'
    default: return 'bg-gray-100 text-gray-700 border-gray-200'
  }
}

const getProviderIcon = (provider: string) => {
  switch (provider) {
    case 'gmail': return 'üìß'
    case 'outlook': return 'üì®'
    case 'microsoft365': return 'üì´'
    case 'smtp': return 'üì§'
    case 'sendgrid': return 'üíå'
    case 'mailgun': return 'üì¨'
    default: return '‚úâÔ∏è'
  }
}

const getReputationColor = (score: number) => {
  if (score >= 80) return 'text-emerald-600'
  if (score >= 60) return 'text-amber-600'
  return 'text-red-600'
}

const getReputationBgColor = (score: number) => {
  if (score >= 80) return 'bg-emerald-500'
  if (score >= 60) return 'bg-amber-500'
  return 'bg-red-500'
}

const formatDate = (dateString: string | null): string => {
  if (!dateString) return 'Never'
  if (typeof window === 'undefined') return dateString
  try {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    })
  } catch {
    return dateString
  }
}

// Main Component
export default function EmailWarmupPage() {
  const [activeTab, setActiveTab] = useState('dashboard')
  const [mailboxes, setMailboxes] = useState<WarmupMailbox[]>(mockMailboxes)
  const [selectedMailbox, setSelectedMailbox] = useState<WarmupMailbox | null>(null)
  const [showAddModal, setShowAddModal] = useState(false)
  const [showMailboxModal, setShowMailboxModal] = useState(false)
  const [isClient, setIsClient] = useState(false)

  useEffect(() => {
    setIsClient(true)
  }, [])

  // Calculate stats
  const stats: WarmupStats = useMemo(() => {
    const activeMailboxes = mailboxes.filter(m => m.status === 'warming' || m.status === 'ready')
    const readyMailboxes = mailboxes.filter(m => m.status === 'ready')
    const errorMailboxes = mailboxes.filter(m => m.status === 'error')
    const avgReputation = mailboxes.length > 0
      ? Math.round(mailboxes.reduce((sum, m) => sum + m.reputationScore, 0) / mailboxes.length)
      : 0
    const avgInbox = mailboxes.length > 0
      ? Number((mailboxes.reduce((sum, m) => sum + m.inboxPlacementRate, 0) / mailboxes.length).toFixed(1))
      : 0

    return {
      totalMailboxes: mailboxes.length,
      activeMailboxes: activeMailboxes.length,
      readyMailboxes: readyMailboxes.length,
      errorMailboxes: errorMailboxes.length,
      avgReputationScore: avgReputation,
      avgInboxRate: avgInbox,
      totalSentToday: mailboxes.reduce((sum, m) => sum + m.todaySent, 0),
      totalReceivedToday: mailboxes.reduce((sum, m) => sum + m.todayReceived, 0),
      totalRepliesToday: Math.round(mailboxes.reduce((sum, m) => sum + (m.todaySent * m.replyRate / 100), 0)),
    }
  }, [mailboxes])

  const handleViewMailbox = (mailbox: WarmupMailbox) => {
    setSelectedMailbox(mailbox)
    setShowMailboxModal(true)
  }

  const handlePauseMailbox = (mailboxId: string) => {
    setMailboxes(prev => prev.map(m => 
      m.id === mailboxId 
        ? { ...m, status: m.status === 'paused' ? 'warming' : 'paused' as const }
        : m
    ))
  }

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        <PageHeader
          title="Email Warmup"
          description="Automated email warming for improved deliverability"
          actions={
            <div className="flex items-center gap-3">
              <button className="px-4 py-2 text-gray-600 border border-gray-300 rounded-lg hover:bg-gray-50 text-sm flex items-center gap-2">
                <span>üìä</span>
                Export Report
              </button>
              <button 
                onClick={() => setShowAddModal(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm flex items-center gap-2"
              >
                <span>‚ûï</span>
                Add Mailbox
              </button>
            </div>
          }
        />

        {/* Tab Navigation */}
        <nav className="-mb-px flex space-x-6 overflow-x-auto" aria-label="Tabs">
          {WARMUP_TABS.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === tab.id
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              {tab.label}
            </button>
          ))}
        </nav>
      </div>

      {/* Tab Content */}
      <div className="px-4 sm:px-6 lg:px-8 pb-8">
        {activeTab === 'dashboard' && (
          <DashboardTab 
            stats={stats} 
            mailboxes={mailboxes} 
            dailyMetrics={mockDailyMetrics}
            onViewMailbox={handleViewMailbox}
            onPauseMailbox={handlePauseMailbox}
            isClient={isClient}
          />
        )}
        {activeTab === 'mailboxes' && (
          <MailboxesTab 
            mailboxes={mailboxes}
            onViewMailbox={handleViewMailbox}
            onPauseMailbox={handlePauseMailbox}
            onAddMailbox={() => setShowAddModal(true)}
            isClient={isClient}
          />
        )}
        {activeTab === 'network' && (
          <NetworkTab peers={mockNetworkPeers} isClient={isClient} />
        )}
        {activeTab === 'analytics' && (
          <AnalyticsTab mailboxes={mailboxes} dailyMetrics={mockDailyMetrics} />
        )}
        {activeTab === 'templates' && (
          <TemplatesTab templates={mockTemplates} />
        )}
        {activeTab === 'blacklist' && (
          <BlacklistTab />
        )}
        {activeTab === 'settings' && (
          <SettingsTab />
        )}
      </div>

      {/* Add Mailbox Modal */}
      {showAddModal && (
        <AddMailboxModal onClose={() => setShowAddModal(false)} />
      )}

      {/* Mailbox Detail Modal */}
      {showMailboxModal && selectedMailbox && (
        <MailboxDetailModal 
          mailbox={selectedMailbox} 
          onClose={() => setShowMailboxModal(false)}
          isClient={isClient}
        />
      )}
    </div>
  )
}

